
import { defineComponent, ref, getCurrentInstance } from "vue";
import { allSheetJsonToObject, jsonToObject } from "./mixins/forLoopMixin";
import {
  System,
  Banner,
  Category,
  CRUD,
  OrderStatus,
  Order,
  AddOn,
  AddOnGroup,
  FetchData,
  Item,
  OrderChoice,
  OrderAddOnGroup,
  OrderAddOn,
  ProxyData,
} from "@/interface";
import ItemDetailComponent from "./components/ItemDetailComponent.vue";
import FooterComponent from "./components/FooterComponent.vue";
import OrderListModalComponent from "./components/OrderListModalComponent.vue";
import AlertModalComponent from "./components/AlertModalComponent.vue";
import OrderHistoryModalComponent from "./components/OrderHistoryModalComponent.vue";
import {
  getCustomerInfoFromLS,
  getOrderListFromLS,
  removeOrderListFromLS,
  setCustomerInfoToLS,
  setOrderListToLS,
} from "./mixins/localStorageMixin";
// import { fetchPost, fetchPostByType } from "./mixins/fetchMixin";
import { arrayFind, arraySum, delay } from "./mixins/generalMixin";
import {
  formatToGoogleSheetDate,
  formatToJSDate,
} from "./mixins/formatDateMixin";
import { fetchPost } from "./mixins/fetchMixin";

export default defineComponent({
  components: {
    ItemDetailComponent,
    FooterComponent,
    OrderListModalComponent,
    AlertModalComponent,
    OrderHistoryModalComponent,
  },
  setup() {
    const { proxy } = getCurrentInstance() as any;
    const proxyData = ref<ProxyData>({
      dataUrl: proxy.$getDataUrl,
      orderUrl: proxy.$getOrderUrl,
      fetchCode: proxy.$fetchCode,
    });

    let systemData = ref<System>({
      companyName: "",
      companyAddress: "",
      companyContact: "",
      companyDescription: "",
      serviceCharge10Percent: false,
      tax6Percent: false,
    });
    let categories = ref<Category[]>([]);
    let items = ref<Item[]>([]);
    let banners = ref<Banner[] | []>([]);
    let orderList = ref<any[]>([]);
    let orderHistories = ref<Order[]>([]);
    let isLoading = ref<boolean>(true);
    let fetchAllSheets = fetch(
      `${proxyData.value.dataUrl}tqx=out:json&sheet=Datas`
    );
    let fetchOrderHistories;

    // first time visit app
    const customerInfo = getCustomerInfoFromLS();
    if (!customerInfo) {
      setCustomerInfoToLS({ deviceId: Date.now() });
    } else {
      const url =
        proxyData.value.orderUrl +
        "sheet=Orders&tq=" +
        encodeURIComponent(`select * where I = ${customerInfo.deviceId}`);
      fetchOrderHistories = fetch(url);
    }

    Promise.all([fetchAllSheets, fetchOrderHistories]).then((res) => {
      res[0].text().then((rep) => {
        const allSheets = allSheetJsonToObject(rep);
        let dataSystem = arrayFind<FetchData>(allSheets, "name", "System")!
          .datas[0]!;
        let dataCategories = arrayFind<FetchData>(
          allSheets,
          "name",
          "Categories"
        )!.datas;
        let dataAddOns = arrayFind<FetchData>(
          allSheets,
          "name",
          "AddOnList"
        )!.datas;
        let dataItems = arrayFind<FetchData>(allSheets, "name", "Items")!.datas;
        let dataBanners = arrayFind<FetchData>(
          allSheets,
          "name",
          "Banners"
        )!.datas;

        // #region system
        window.document.title = `${dataSystem.companyName} - Menu Panda`;
        // #endregion

        // #region addOnList
        dataAddOns = dataAddOns.map(
          (addOnGroup: AddOnGroup, dataAddOnListindex: number) => {
            const obj: AddOnGroup = {
              name: addOnGroup.name,
              addOns: [],
              noCharges: false,
              minimumSelect: addOnGroup.minimumSelect,
              maximumSelect: addOnGroup.maximumSelect,
              defaultSelect: addOnGroup.defaultSelect,
              duplicateSelect: addOnGroup.duplicateSelect,
            };
            const addOnGroupChoices = addOnGroup
              .choice!.split(",")
              .map((type: string) => type.trim());
            let addOnGroupPrices = addOnGroup
              .price!.split(",")
              .map((type: string) => parseFloat(type.trim()));

            if (
              addOnGroupPrices.length >= 2 &&
              addOnGroupChoices.length !== addOnGroupPrices.length
            ) {
              alert("Please inform programmer! Error Code: 02");
              console.warn(
                "dataItem from GS's AddOn row: " + (dataAddOnListindex + 2)
              );
            }

            if (addOnGroupPrices.length === 1 && isNaN(addOnGroupPrices[0])) {
              addOnGroupPrices = [];
            }

            // push list
            addOnGroupChoices.forEach((choice: string, index: number) => {
              obj.addOns.push({
                name: choice,
                price: addOnGroupPrices[index] ?? 0,
              } as AddOn);
            });

            obj.noCharges = obj.addOns.every(
              (addOn: AddOn) => addOn.price === 0
            );

            return obj;
          }
        );
        // #endregion

        // #region item
        dataItems = dataItems.map((item: Item, dataItemIndex: number) => {
          const obj: Item = {
            categoryName: item.categoryName,
            image: item.image,
            code: item.code,
            name: item.name,
            description: item.description,
            note: item.note,
            choices: [],
            addOnGroups: [],
            orderList: [],
            orderHistoryList: [],
          };

          // build prices
          if (item.choice === "") {
            obj.choices.push({
              name: "", // empty as name
              price: +item.price!,
            });
          } else {
            const choiceNames = item.choice!.split(",");
            const choicePrices = item.price!.split(",");
            if (choiceNames.length !== choicePrices.length) {
              alert("Please inform programmer! Error Code: 03");
              console.warn(
                "dataItem from GS's Prices row: " + (dataItemIndex + 2)
              );
            }
            choiceNames.forEach((choiceName, index) => {
              obj.choices.push({
                name: choiceName,
                price: +choicePrices[index].toString().trim(),
              });
            });
          }

          // push addOnList
          if (item.addOn !== "") {
            const addOnNames = item.addOn!.split(",");

            //  fitler,  addOnNames
            addOnNames.forEach((name: string) => {
              const findedAddOns = arrayFind<AddOnGroup>(
                dataAddOns,
                "name",
                name
              )!;
              obj.addOnGroups.push(findedAddOns);
            });
          }

          return obj;
        });
        // #endregion

        // #region category
        dataCategories = dataCategories.map(
          (category: Category): Category => ({
            name: category.name,
            items: dataItems.filter(
              (item: Item) => item.categoryName === category.name
            ),
          })
        );

        // #endregion

        // #region banner
        dataBanners = dataBanners.map(
          (banner: Banner): Banner => ({
            description: banner.description,
            image: banner.image,
            itemCode: banner.itemCode,
            item: dataItems.find((item: Item) => item.code === banner.itemCode),
          })
        );
        // #endregion

        // #region orderList
        let dataOrderList = getOrderListFromLS() ?? [];
        dataOrderList.forEach((orderChoice: OrderChoice) => {
          const findedItem = arrayFind<Item>(
            dataItems,
            "code",
            orderChoice.itemCode
          );
          if (findedItem) {
            findedItem.orderList.push(orderChoice);
          } else {
            return; // auto remove the LS's orderList
          }
        });
        removeOrderListFromLS(); // clean up
        // #endregion

        systemData.value = dataSystem;
        categories.value = dataCategories;
        items.value = dataItems;
        banners.value = dataBanners;
        orderList.value = dataOrderList;
        isLoading.value = false;

        // orderHistory
        if (res[1]) {
          res[1].text().then((rep) => {
            let dataOrderHistories: Order[] = jsonToObject(rep);

            if (dataOrderHistories && dataOrderHistories.length > 0) {
              dataOrderHistories = dataOrderHistories.reduce(
                (arr: Order[], orderHistory: Order) => {
                  // step1: filter  status
                  if (
                    [
                      OrderStatus.waiting,
                      OrderStatus.queueing,
                      OrderStatus.preparing,
                    ].includes(orderHistory.status)
                  ) {
                    const createdDate = formatToJSDate(
                      orderHistory.createdDate
                    );
                    const current = new Date().getTime() / 1000 / 60;
                    const orderCreatedDate = createdDate.getTime() / 1000 / 60;

                    // step2: more than 2 hour
                    if (current - orderCreatedDate >= 120) {
                      orderHistory.note = `Error! order over 2 hours "${orderHistory.status}" to "${OrderStatus.cancelled}"`;
                      orderHistory.status = OrderStatus.cancelled;
                      orderHistory.createdDate = formatToGoogleSheetDate(
                        new Date(formatToJSDate(orderHistory.createdDate))
                      );

                      fetchPost<Order>(
                        CRUD.update,
                        "Orders",
                        proxyData.value.fetchCode,
                        orderHistory
                      );
                    } else {
                      orderHistory.orderList = JSON.parse(
                        orderHistory.orderList as string
                      );
                      arr.push(orderHistory);
                    }
                  }
                  return arr;
                },
                []
              );

              if (dataOrderHistories.length > 0) {
                // checking
                if (dataOrderHistories.length >= 2) {
                  console.warn("Error! 1 order history!");
                }
                // step3:  orderHisotry
                // push data to item's orderHistory
                (dataOrderHistories[0].orderList as OrderChoice[]).forEach(
                  (orderChoice: OrderChoice) => {
                    const findedItem = arrayFind<Item>(
                      dataItems,
                      "code",
                      orderChoice.itemCode
                    );
                    if (!findedItem) {
                      console.warn("auto remove due to code no found!");
                    } else {
                      findedItem.orderHistoryList.push(orderChoice);
                    }
                  }
                );
              }

              orderHistories.value = dataOrderHistories;
            }
          });
        }
      });
    });

    return {
      proxyData,
      systemData,
      banners,
      categories,
      orderHistories,
      items,
      orderList,
      isLoading,
    };
  },
  data() {
    return {
      diplayItemCode: null as null | string,
      totalOrderAmount: 0,
      totalOrderQty: 0,

      isShowOrderListModal: false,
      isShowOrderListModalLoading: false,
      isShowAlertModal: false,
      isShowOrderHistoryModal: false,
      orderNo: "",
    };
  },
  methods: {
    showItemDetail(code: string) {
      this.diplayItemCode = code;
    },
    hideItemDetail() {
      this.diplayItemCode = null;
    },
    updateItemAndItemPrice(item: Item) {
      return;
      // item.added = arraySum(item.prices, "added");
      // item.prices.forEach((itemPrice: Price) => {
      //   itemPrice.sumPrice = itemPrice.price * itemPrice.added;
      // });
    },
    showOrderListModal() {
      this.isShowOrderListModal = true;
    },
    hideOrderListModal() {
      this.isShowOrderListModal = false;
    },
    setOrderNo(value: string) {
      this.orderNo = value;
    },
    showAlertModal() {
      this.isShowAlertModal = true;
    },
    hideAlertModal() {
      this.isShowAlertModal = false;
    },
    showOrderHistoryModal() {
      this.isShowOrderHistoryModal = true;
    },
    hideOrderHistoryModal() {
      this.isShowOrderHistoryModal = false;
    },
  },
  watch: {
    items: {
      handler(items: Item[]) {
        let totalOrderAmount = 0;
        let choiceOverrallTotal = 0;
        let totaOrderQty = 0;
        const LSOrderList: OrderChoice[] = [];

        items.forEach((item: Item) => {
          item.orderList.forEach((orderChoice: OrderChoice) => {
            orderChoice.addOnGroups.forEach(
              (orderAddOnGroup: OrderAddOnGroup) => {
                orderAddOnGroup.addOns.forEach((orderAddOn: OrderAddOn) => {
                  orderAddOn.total =
                    orderAddOn.unitPrice * orderAddOn.qty * orderChoice.qty;
                  choiceOverrallTotal += orderAddOn.total;
                });
              }
            );
            orderChoice.total = orderChoice.unitPrice * orderChoice.qty;
            orderChoice.overallTotal = orderChoice.total + choiceOverrallTotal;

            totalOrderAmount += orderChoice.overallTotal;
            totaOrderQty += orderChoice.qty;
            choiceOverrallTotal = 0; // reset to zero

            LSOrderList.push(orderChoice);
          });
        });

        this.totalOrderAmount = totalOrderAmount;
        this.totalOrderQty = totaOrderQty;

        removeOrderListFromLS();
        if (LSOrderList.length > 0) {
          setOrderListToLS(LSOrderList);
        }
      },
      deep: true,
    },
    orderHistories(newValues: Order[]) {
      if (newValues.length > 0) {
        this.orderNo = newValues[0].orderNo!;
        delay((this.isShowOrderHistoryModal = true), 1000);
      }
    },
  },
});
