
import {
  ChoiceStatus,
  CRUD,
  ImageSize,
  Item,
  Order,
  OrderAddOn,
  OrderAddOnGroup,
  OrderChoice,
  OrderStatus,
  ProxyData,
  System,
} from "@/interface";
import { fetchPost, fetchPostByType } from "@/mixins/fetchMixin";
import { jsonToObject } from "@/mixins/forLoopMixin";
import {
  formatToGoogleSheetDate,
  formatToJSDate,
} from "@/mixins/formatDateMixin";
import {
  arrayFind,
  chargeDetail,
  convertFlickrImage,
  delay,
} from "@/mixins/generalMixin";
import {
  getCustomerInfoFromLS,
  removeOrderListFromLS,
} from "@/mixins/localStorageMixin";
import { defineComponent, PropType } from "vue";
import ItemComponent from "../components/ItemComponent.vue";

export default defineComponent({
  props: {
    items: {
      type: Object as PropType<Item[]>,
      required: true,
    },
    isShowOrderListModal: {
      type: Boolean,
      required: true,
    },
    totalOrderAmount: {
      type: Number,
      required: true,
    },
    systemData: {
      type: Object as PropType<System>,
      required: true,
    },
    proxyData: {
      type: Object as PropType<ProxyData>,
      required: true,
    },
    orderHistory: {
      type: Object as PropType<Order>,
    },
  },
  emits: ["hideOrderListModal", "setOrderNo", "showAlertModal"],
  data() {
    return {
      ImageSize: ImageSize,
      isSubmitLoading: false,
    };
  },
  components: {
    ItemComponent,
  },
  computed: {
    filteredItemOrderList(): Item[] {
      return this.items.filter((item: Item) => item.orderList.length > 0);
    },
    filteredItemOrderHistoryList(): Item[] {
      return this.items.filter(
        (item: Item) => item.orderHistoryList.length > 0
      );
    },
    chargeDetail() {
      return chargeDetail(this.systemData, this.totalOrderAmount);
    },
  },
  methods: {
    hideOrderListModal() {
      this.$emit("hideOrderListModal");
    },
    confirmOrder() {
      this.isSubmitLoading = true;
      const orderListOrderChoice = this.filteredItemOrderList.reduce(
        (arr: OrderChoice[], item: Item) => {
          return arr.concat(item.orderList);
        },
        []
      );

      orderListOrderChoice.forEach((orderChoice: OrderChoice) => {
        orderChoice.status = ChoiceStatus.queueing;
      });

      const postData: Order = {
        orderNo: "", // GS need dataType for padStart
        status: OrderStatus.waiting,
        tableNo: 0,
        orderList: JSON.stringify(orderListOrderChoice),
        total: this.totalOrderAmount,
        nettTotal: this.chargeDetail.nettTotal,
        note: "",
        createdDate: formatToGoogleSheetDate(new Date()),
        deviceId: getCustomerInfoFromLS().deviceId,
      };

      fetchPostByType("createOrder", this.proxyData.fetchCode, postData)
        .then(() => {
          this.filteredItemOrderList.forEach(
            (item: Item) => (item.orderList = [])
          );
          removeOrderListFromLS();
          // get new order Id
          fetch(
            `${this.proxyData.orderUrl}sheet=Orders&tq=${encodeURIComponent(
              "select * where I = "
            )}${getCustomerInfoFromLS().deviceId} and C = ${encodeURIComponent(
              `"${OrderStatus.waiting}"`
            )}`
          )
            .then((res) => res.text())
            .then((rep) => {
              this.isSubmitLoading = false;
              this.$emit("setOrderNo", jsonToObject(rep)[0].orderNo!);
              delay(this.hideOrderListModal(), 1000);
              delay(this.$emit("showAlertModal"), 2000);
            });
        })
        .catch(() => {
          alert(
            "Error! Submit fail, please inform waiters and order manually!"
          );
          console.warn("Submit Order Error! Click Confirm Order");
        });
    },
    pushOrderList(items: Item[], orderHistoryOrderChoice: OrderChoice) {
      const findedItem = arrayFind<Item>(
        items,
        "code",
        orderHistoryOrderChoice.itemCode
      )!;
      orderHistoryOrderChoice.status = ChoiceStatus.queueing;
      findedItem.orderList.push(orderHistoryOrderChoice);
    },
    updateOrderChoiceStatusAndQty(
      orderHistoryOrderChoice: OrderChoice,
      orderListOrderChoice: OrderChoice
    ) {
      if (orderHistoryOrderChoice.status === ChoiceStatus.completed) {
        orderListOrderChoice.completedQty = orderHistoryOrderChoice.qty;
      }
      orderListOrderChoice.status = ChoiceStatus.queueing;
      orderListOrderChoice.qty += orderHistoryOrderChoice.qty;
    },
    combineOrder() {
      this.isSubmitLoading = true;

      // 
      // orderHistory  orderList
      // orderList  queueing 
      this.filteredItemOrderList.forEach((item: Item) => {
        item.orderList.forEach((orderList: OrderChoice) => {
          orderList.status = ChoiceStatus.queueing;
        });
      });

      const notMatchOrderChoices: OrderChoice[] = [];
      for (let h = 0; h < this.filteredItemOrderHistoryList.length; h++) {
        const orderHistory = this.filteredItemOrderHistoryList[h];
        const findedItem = arrayFind<Item>(
          this.filteredItemOrderList,
          "code",
          orderHistory.code
        );

        // step 1  item's code
        if (!findedItem) {
          notMatchOrderChoices.push(...orderHistory.orderHistoryList);
          break;
        }

        // deep 1 : choice's name
        for (let i = 0; i < orderHistory.orderHistoryList.length; i++) {
          const orderHistoryOrderChoice = orderHistory.orderHistoryList[i];
          const findedOrderList = arrayFind<OrderChoice>(
            findedItem.orderList,
            "name",
            orderHistoryOrderChoice.name
          );

          // step 2  choice's name
          //  choice's name
          if (!findedOrderList) {
            notMatchOrderChoices.push(orderHistoryOrderChoice);
            continue;
          }
          
          //  addOnGroups.length === 0
          //  looping
          // deep 2 : addOnGroup's name
          let isBreakAddOnGroup = false;
          for (let j = 0; j < orderHistoryOrderChoice.addOnGroups.length; j++) {
            const orderHistoryAddOnGroup =
              orderHistoryOrderChoice.addOnGroups[j];
            const findedOrderAddOnGroup = arrayFind<OrderAddOnGroup>(
              findedOrderList.addOnGroups,
              "name",
              orderHistoryAddOnGroup.name
            );

            // step 3 :  addOnGroup's name
            //  addOnGroup's name
            if (!findedOrderAddOnGroup) {
              isBreakAddOnGroup = true;
              break;
            }

            // deep 3 : addOn's name & qty
            let isBreakAddOns = false;
            for (let k = 0; k < orderHistoryAddOnGroup.addOns.length; k++) {
              const orderHistoryAddOn = orderHistoryAddOnGroup.addOns[k];
              const findedOrderAddOn = arrayFind<OrderAddOn>(
                findedOrderAddOnGroup.addOns,
                "name",
                orderHistoryAddOn.name
              );

              // step 4 :  addOn's name
              //  addOn's name 
              // addOn's qty 
              // addOns.length 
              if (
                !findedOrderAddOn ||
                orderHistoryAddOn.qty !== findedOrderAddOn.qty ||
                orderHistoryAddOnGroup.addOns.length !== findedOrderAddOnGroup.addOns.length
              ) {
                isBreakAddOns = true;
                break;
              }
            }

            //  addOn's name & qty, 
            // isBreakAddOnGroup = true
            if (isBreakAddOns) {
              isBreakAddOnGroup = true;
              break;
            }
          }

          //  addOnGroup's name, 
          if (isBreakAddOnGroup) {
            notMatchOrderChoices.push(orderHistoryOrderChoice);
          } else {
            this.updateOrderChoiceStatusAndQty(
              orderHistoryOrderChoice,
              findedOrderList
            );
          }
        }
      }

      notMatchOrderChoices.forEach((orderChoice: OrderChoice) => {
        const findedItem = arrayFind<Item>(
          this.items,
          "code",
          orderChoice.itemCode
        )!;
        findedItem.orderList.push(orderChoice);
      });

      const renderOrderListOrderChoices = this.filteredItemOrderList.reduce(
        (arr: OrderChoice[], item: Item) => {
          return arr.concat(item.orderList);
        },
        []
      );

      this.orderHistory!.orderList = JSON.stringify(
        renderOrderListOrderChoices
      );
      this.orderHistory!.createdDate = formatToGoogleSheetDate(
        new Date(formatToJSDate(this.orderHistory!.createdDate))
      );

      fetchPost<Order>(
        CRUD.update,
        "Orders",
        this.proxyData.fetchCode,
        this.orderHistory!
      )
        .then(() => {
          this.filteredItemOrderList.forEach(
            (item: Item) => (item.orderList = [])
          );
          removeOrderListFromLS();

          this.isSubmitLoading = false;
          delay(this.hideOrderListModal(), 1000);
          delay(this.$emit("showAlertModal"), 2000);
        })
        .catch(() => {
          alert(
            "Error! Submit fail, please inform waiters and order manually!"
          );
          console.warn("Submit Order Error! Click Combine Order");
        });
    },
    convertFlickrImage(imageLink: string, size: ImageSize | null) {
      return convertFlickrImage(imageLink, size);
    },
  },
  watch: {
    items: {
      handler(items: Item[]) {
        const isEmptyOrderList = items.every(
          (item: Item) => item.orderList.length === 0
        );
        if (isEmptyOrderList) {
          this.$emit("hideOrderListModal");
        }
      },
      deep: true,
    },
  },
});
