
import { ImageSize, Item, Order, OrderChoice, System } from "@/interface";
import { dateToString } from "@/mixins/formatDateMixin";
import { chargeDetail, convertFlickrImage } from "@/mixins/generalMixin";
import { defineComponent, PropType } from "vue";
import ItemComponent from "../components/ItemComponent.vue";

export default defineComponent({
  props: {
    isShowOrderHistoryModal: {
      type: Boolean,
      required: true,
    },
    items: {
      type: Object as PropType<Item[]>,
      required: true,
    },
    orderHistories: {
      type: Object as PropType<Order[]>,
      required: true,
    },
    systemData: {
      type: Object as PropType<System>,
      required: true,
    },
  },
  emits: ["hideOrderHistoryModal"],
  components: { ItemComponent },
  computed: {
    filteredItems(): Item[] {
      return this.items.filter(
        (item: Item) => item.orderHistoryList.length > 0
      );
    },
    orderHistory() {
      return this.orderHistories[0];
    },
    chargeDetail() {
      return chargeDetail(this.systemData, this.totalOrderAmount);
    },
    totalOrderAmount() {
      console.log(this.filteredItems);
      
      return this.filteredItems.reduce((total: number, item: Item) => {
        return (total += item.orderHistoryList.reduce(
          (total2: number, orderList: OrderChoice) =>
            (total2 += orderList.overallTotal),
          0
        ));
      }, 0);
    },
  },
  methods: {
    hideOrderHistoryModal() {
      this.$emit("hideOrderHistoryModal");
    },
    convertFlickrImage(imageLink: string, size: ImageSize | null) {
      return convertFlickrImage(imageLink, size);
    },
    dateToString(value: string) {
      return dateToString(value);
    },
  },
});
