import { LSCustomerInfo, OrderChoice } from "@/interface";

export enum LocalStorage {
    menuPandaCustomerInfo = 'menuPandaCustomerInfo',
    menuPandaOrderList = 'menuPandaOrderList',
}

export function setCustomerInfoToLS(value: LSCustomerInfo) {
    const data = JSON.stringify(value);
    localStorage.setItem(LocalStorage.menuPandaCustomerInfo, data);
}
export function getCustomerInfoFromLS(): LSCustomerInfo {
    return JSON.parse(
        localStorage.getItem(LocalStorage.menuPandaCustomerInfo)!
    );
}
export function removeCustomerInfoFromLS() {
    localStorage.removeItem(LocalStorage.menuPandaCustomerInfo)
}

export function setOrderListToLS(values: OrderChoice[]) {
    const datas = JSON.stringify(values);
    localStorage.setItem(LocalStorage.menuPandaOrderList, datas);
}
export function getOrderListFromLS(): OrderChoice[] {
    return JSON.parse(
        localStorage.getItem(LocalStorage.menuPandaOrderList)!
    );
}
export function removeOrderListFromLS() {
    localStorage.removeItem(LocalStorage.menuPandaOrderList)
}
