
import {
  AddOn,
  ImageSize,
  Item,
  Choice,
  OrderChoice,
  OrderAddOn,
  OrderAddOnGroup,
  AddOnGroup,
} from "@/interface";
import {
  arrayFind,
  arrayFindIndex,
  arraySum,
  convertFlickrImage,
} from "@/mixins/generalMixin";
import { defineComponent } from "vue";

export default defineComponent({
  props: ["diplayItemCode", "items"],
  emits: ["hideItemDetail", "updateAddOnToOrder"],
  data() {
    return {
      item: {} as Item,
      prevOrderList: [] as OrderChoice[],
      currentOrderList: [] as OrderChoice[],
      ImageSize: ImageSize,
      warningClass: "",
      showCloseButton: true,
      showResetButton: false,
    };
  },
  methods: {
    buildTerm(addOnGroup: AddOnGroup) {
      let texts = [];
      if (addOnGroup.minimumSelect !== null) {
        texts.push(`min: ${addOnGroup.minimumSelect}`);
      }
      if (addOnGroup.maximumSelect !== null) {
        texts.push(`max: ${addOnGroup.maximumSelect}`);
      }
      if (addOnGroup.duplicateSelect) {
        texts.push(`multiple`);
      }
      return `${texts.join(", ")}`;
    },
    updateChoiceQty(choice: Choice, qty: number) {
      // clear style class due to must selected choice before press add on
      this.warningClass = "";

      // no addOn
      if (this.item.addOnGroups.length === 0) {
        const findedCurrentOrderList = arrayFind<OrderChoice>(
          this.currentOrderList,
          "name",
          choice.name
        );

        if (!findedCurrentOrderList) {
          this.pushCurrentOrder(choice, qty);
        } else {
          findedCurrentOrderList.qty += qty;
        }
      } else {
        // clear all selected then push
        this.currentOrderList = [];
        this.pushCurrentOrder(choice, qty);

        // checking addOnGroup defaultSelect
        this.item.addOnGroups.forEach((addOnGroup: AddOnGroup) => {
          if (addOnGroup.defaultSelect !== null) {
            const addOnIndex = addOnGroup.defaultSelect! - 1;
            const defaultSelectAddOn = addOnGroup.addOns[addOnIndex];
            this.pushCurrentOrderAddOnGroup(addOnGroup, defaultSelectAddOn, 1);
          }
        });
      }
      this.showCloseButton = false;
      this.showResetButton = false;
    },
    removeChoice(choice: Choice) {
      const findedIndex = arrayFindIndex<OrderChoice>(
        this.currentOrderList,
        "name",
        choice.name
      );
      this.currentOrderList.splice(findedIndex, 1);
      if (this.currentOrderList.length === 0) {
        this.showCloseButton = true;
        this.showResetButton = false;
      }
    },
    pushCurrentOrder(choice: Choice, qty: number) {
      const obj: OrderChoice = {
        itemCode: this.item.code,
        name: choice.name,
        unitPrice: choice.price,
        qty: qty,
        total: 0, // 
        addOnGroups: [],
        overallTotal: 0, // 
      };
      this.currentOrderList.push(obj);
    },
    pushCurrentOrderAddOnGroup(
      addOnGroup: AddOnGroup,
      addOn: AddOn,
      qty: number
    ) {
      const obj: OrderAddOnGroup = {
        name: addOnGroup.name,
        addOns: [
          {
            name: addOn.name,
            unitPrice: addOn.price,
            qty: qty,
            total: addOn.price * qty,
          },
        ],
      };
      this.currentOrderList[0].addOnGroups.push(obj);
    },
    pushCurrentOrderAddOn(
      currentOrderAddOnGroup: OrderAddOnGroup,
      addOn: AddOn,
      qty: number
    ) {
      const obj: OrderAddOn = {
        name: addOn.name,
        unitPrice: addOn.price,
        qty: qty,
        total: 0, // 
      };
      currentOrderAddOnGroup.addOns.push(obj);
    },
    updateAddOnQty(addOnGroup: AddOnGroup, addOn: AddOn, qty: number) {
      // checking
      if (this.currentOrderList.length === 0) {
        alert("Please select top choice first.");
        this.warningClass = "warning";
        document.getElementById("itemComponentDetailContainer")!.scrollTop = 0;
        return;
      }
      if (this.currentOrderList.length > 1) {
        alert("Error !!!");
        console.warn("got add on config = 1 order list");
      }

      // finded 1
      const findedItemAddOnGroup = arrayFind<AddOnGroup>(
        this.item.addOnGroups,
        "name",
        addOnGroup.name
      );
      const findedCurrentOrderAddOnGroup = arrayFind<OrderAddOnGroup>(
        this.currentOrderList[0].addOnGroups,
        "name",
        addOnGroup.name
      );
      const findedCurrentOrderAddOnGroupIndex = arrayFindIndex<OrderAddOnGroup>(
        this.currentOrderList[0].addOnGroups,
        "name",
        addOnGroup.name
      );

      const config = findedItemAddOnGroup!;

      // 
      if (!findedCurrentOrderAddOnGroup) {
        this.pushCurrentOrderAddOnGroup(addOnGroup, addOn, qty);
        return;
      }

      // finded 2
      const findedCurrentOrderAddOn = arrayFind<OrderAddOn>(
        findedCurrentOrderAddOnGroup.addOns,
        "name",
        addOn.name
      );
      const findedCurrentOrderAddOnIndex = arrayFindIndex<OrderAddOn>(
        findedCurrentOrderAddOnGroup.addOns,
        "name",
        addOn.name
      );

      // checking
      if (!findedItemAddOnGroup) {
        alert("Error !!!");
        console.warn("Must find! Clear all cache and redirect to home");
        return;
      }

      // no charge = chip
      if (config.noCharges) {
        // mode
        //  / >
        if (findedCurrentOrderAddOn) {
          // config.minimumSelect
          if (config.minimumSelect && config.minimumSelect > 0) {
            const selectedCount = findedCurrentOrderAddOnGroup.addOns.length;
            if (selectedCount > config.minimumSelect) {
              findedCurrentOrderAddOnGroup.addOns.splice(
                findedCurrentOrderAddOnIndex,
                1
              );
            }
          } else {
            findedCurrentOrderAddOnGroup.addOns.splice(
              findedCurrentOrderAddOnIndex,
              1
            );
          }
        } else {
          // config.maximumSelect
          if (config.maximumSelect && config.maximumSelect > 0) {
            const selectedCount = findedCurrentOrderAddOnGroup.addOns.length;
            if (selectedCount >= config.maximumSelect) {
              findedCurrentOrderAddOnGroup.addOns.splice(0, 1);
            }
            this.pushCurrentOrderAddOn(
              findedCurrentOrderAddOnGroup,
              addOn,
              qty
            );
          }
        }

        // 
        if (findedCurrentOrderAddOnGroup.addOns.length === 0) {
          this.currentOrderList[0].addOnGroups.splice(
            findedCurrentOrderAddOnGroupIndex,
            1
          );
        }
      } else {
        // mode
        if (config.maximumSelect && config.maximumSelect > 0) {
          const selectedCount = arraySum(
            findedCurrentOrderAddOnGroup.addOns,
            "qty"
          );
          if (selectedCount >= config.maximumSelect) {
            alert(
              `Warning, You have hit the maximum (${config.maximumSelect})select.`
            );
            return;
          } else {
            if (!findedCurrentOrderAddOn) {
              this.pushCurrentOrderAddOn(
                findedCurrentOrderAddOnGroup,
                addOn,
                qty
              );
            } else {
              // mode
              if (config.duplicateSelect) {
                findedCurrentOrderAddOn.qty += qty;

                // update order choice total
                const addOnGroupTotal =
                  this.currentOrderList[0].addOnGroups.reduce(
                    (total: number, addOnGroup: OrderAddOnGroup) =>
                      (total += arraySum(addOnGroup.addOns, "total")),
                    0
                  );
                this.currentOrderList[0].overallTotal =
                  this.currentOrderList[0].total + addOnGroupTotal;
              }
            }
          }
        }
      }

      this.showCloseButton = false;
    },
    removeAddOn(addOnGroup: AddOnGroup, addOn: AddOn) {
      const findedCurrentOrderAddOnGroup = arrayFind<OrderAddOnGroup>(
        this.currentOrderList[0].addOnGroups,
        "name",
        addOnGroup.name
      );
      const findedCurrentOrderAddOnIndex = arrayFindIndex<OrderAddOn>(
        findedCurrentOrderAddOnGroup!.addOns,
        "name",
        addOn.name
      );
      findedCurrentOrderAddOnGroup?.addOns.splice(
        findedCurrentOrderAddOnIndex,
        1
      );
    },
    getChoiceQty(choice: Choice) {
      const finded = arrayFind<OrderChoice>(
        this.currentOrderList,
        "name",
        choice.name
      );
      return finded ? finded.qty : false;
    },
    getAddOnQty(addOnGroup: AddOnGroup, addOn: AddOn) {
      if (this.currentOrderList.length === 0) {
        return false;
      }

      const findedCurrentOrderAddOnGroup = arrayFind<OrderAddOnGroup>(
        this.currentOrderList[0].addOnGroups,
        "name",
        addOnGroup.name
      );

      if (!findedCurrentOrderAddOnGroup) {
        return false;
      } else {
        const findedCurrentOrderAddOns = arrayFind<OrderAddOn>(
          findedCurrentOrderAddOnGroup!.addOns,
          "name",
          addOn.name
        );
        return findedCurrentOrderAddOns ? findedCurrentOrderAddOns.qty : false;
      }
    },
    getAddOnConfigDuplicateSelect(
      orderChoice: OrderChoice,
      addOnGroup: OrderAddOnGroup
    ) {
      const findedItem = arrayFind<Item>(
        this.items,
        "code",
        orderChoice.itemCode
      )!;
      return arrayFind<AddOnGroup>(
        findedItem.addOnGroups,
        "name",
        addOnGroup.name
      )!.duplicateSelect;
    },
    close() {
      if (this.currentOrderList.length === 0) {
        this.$emit("hideItemDetail");
        return;
      }

      // checking validation: addOn minimum
      let alertText = "";
      this.item.addOnGroups.forEach((addOnGroup: AddOnGroup) => {
        if (
          addOnGroup.minimumSelect !== undefined &&
          addOnGroup.minimumSelect > 0
        ) {
          const findedCurrentOrderAddOnGroup = arrayFind<OrderAddOnGroup>(
            this.currentOrderList[0].addOnGroups,
            "name",
            addOnGroup.name
          )!;
          let totalQty = null;
          if (findedCurrentOrderAddOnGroup) {
            totalQty = arraySum(findedCurrentOrderAddOnGroup.addOns, "qty");
          }

          if (
            !findedCurrentOrderAddOnGroup ||
            totalQty < addOnGroup.minimumSelect
          ) {
            alertText += `At least select ${addOnGroup!.minimumSelect} "${
              addOnGroup.name
            }"\n`;
          }
        }
      });
      if (alertText !== "") {
        this.showResetButton = true;
        alert(alertText);
        return;
      }

      //  orderList =  push
      if (this.item.orderList.length === 0) {
        this.item.orderList.push(...this.currentOrderList);
        this.currentOrderList = [];
        this.$emit("hideItemDetail");
        return;
      }

      this.currentOrderList.forEach((currentOrderChoice: OrderChoice) => {
        const findedItemOrder = arrayFind<OrderChoice>(
          this.item.orderList,
          "name",
          currentOrderChoice.name
        );
        if (!findedItemOrder) {
          this.item.orderList.push(currentOrderChoice);
        } else {
          if (this.item.addOnGroups.length === 0) {
            findedItemOrder.qty += currentOrderChoice.qty;
          } else {
            const match = currentOrderChoice.addOnGroups.every(
              (currentAddOnGroup: OrderAddOnGroup) => {
                const findedItemOrderAddOnGroup = arrayFind<OrderAddOnGroup>(
                  findedItemOrder.addOnGroups,
                  "name",
                  currentAddOnGroup.name
                );
                if (
                  !findedItemOrderAddOnGroup ||
                  currentAddOnGroup.addOns.length !==
                    findedItemOrderAddOnGroup.addOns.length
                ) {
                  return false;
                } else {
                  return currentAddOnGroup.addOns.every(
                    (currentAddOn: OrderAddOn) => {
                      const findedItemOrderAddOn = arrayFind<OrderAddOn>(
                        findedItemOrderAddOnGroup.addOns,
                        "name",
                        currentAddOn.name
                      );
                      return findedItemOrderAddOn
                        ? currentAddOn.qty === findedItemOrderAddOn.qty
                        : false;
                    }
                  );
                }
              }
            );

            if (match) {
              findedItemOrder.qty++;
            } else {
              this.item.orderList.push(currentOrderChoice);
            }
          }
        }
      });

      this.$emit("hideItemDetail");
      return;
    },
    reset() {
      this.currentOrderList = [];
      this.showCloseButton = true;
    },
    convertFlickrImage(imageLink: string, size: ImageSize | null) {
      return convertFlickrImage(imageLink, size);
    },
  },
  watch: {
    diplayItemCode(newVal) {
      if (newVal !== null) {
        this.item = arrayFind<Item>(this.items, "code", newVal)!;
        this.prevOrderList = [...this.item.orderList];
        this.prevOrderList.forEach((orderChoice: OrderChoice) => {
          orderChoice.addOnGroups = [...orderChoice.addOnGroups];
          orderChoice.addOnGroups.forEach(
            (orderAddOnGroup: OrderAddOnGroup) => {
              orderAddOnGroup = { ...orderAddOnGroup };
              orderAddOnGroup.addOns = [...orderAddOnGroup.addOns];
            }
          );
        });

        // reset
        this.showCloseButton = true;
        this.showResetButton = false;
        this.currentOrderList = [];
      }
    },
  },
});
