
import { delay } from "@/mixins/generalMixin";
import { defineComponent } from "vue";

export default defineComponent({
  props: ["isShowAlertModal", "orderNo"],
  emits: ["hideAlertModal"],
  methods: {
    hideAlertModal() {
      this.$emit("hideAlertModal");
      delay((window.location.href = "/"), 1000);
    },
  },
});
